import React from 'react';
import { ContactText } from '../portfolio_data/TextData'
import './Contact.css'

const Contact = () => {
    const { Email, link } = ContactText
    const marginStyle={ marginRight: "20px" }

    return (
        <div id="contact" className="section">
            <h1 className="section-title" id="contact-title">CONTACT</h1>

            <div id="contact-contents">
                <div id="contact-email">Email : {Email}</div>                
                <div style={{textAlign: 'center', marginTop: '30px'}}>

                <div className="contact-link-container">
                {link.map((element, index) => {
                    return (
                        <div key={index} className="contact-each-link">
                            <a className="contact-link-element" href={element.url} target="_blank" rel="noopener noreferrer"
                            style={marginStyle}>
                            <img className="contact-logo" src={element.logo} alt=''></img>
                            </a>                              
                            <div className="contanct-logo-caption">{element.name}</div>
                        </div>
                    )
                })}
                </div>
                    
                </div>
            </div>
        </div>
    )
}

export default Contact
