import React from 'react';
import Modal from 'react-modal';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import closebtn from '../images/closebtn.png';  //close button
import './PortfolioModal.css'

const modalStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
  },
};

// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('#root')

const PortfolioModal = ({ modalOpen, toggleModal, data }) => {
  const { 
    title, span, team, contents, stacks, 
    files, reference, githubRepo
  } = data

  return (
    <div>
      <Modal
        isOpen={modalOpen}
        onRequestClose={toggleModal}
        style={{...modalStyles, 'overflow-y': 'scroll'}}
        contentLabel="Example Modal">
      
      <div className="modal-box">     
          <Carousel
          useKeyboardArrows          
            showThumbs={false}>
            {files.map((eachFile, index) => {
              return (
                <div key={index} className="carousel-container">
                  {eachFile.type === 'image' ? 
                    <div className="portfolioModal-media" 
                      style={{
                        background: `url(${eachFile.file})`, 
                        backgroundSize: 'contain', 
                        backgroundPosition: 'center',
                        backgroundRepeat: 'no-repeat'
                      }}>
                    </div> 
                    :
                    <video className="portfolioModal-media" controls>
                      <source src={eachFile.file} type="video/mp4" />
                    </video>}
                  <p className="carousel-caption">{eachFile.description}</p>
                </div>
              )}
          )}
          </Carousel>     

          <div className="portfolioModal-text">
            <h1 id="portfolioModal-main-title">{title}</h1>     
            <ul className="portfolioModal-ul">       
              <li>{team}</li>
              <li>개발 기간 : {span}</li>
            </ul>
            <h2 className="portfolioModal-text-subtitle">About project</h2>
            <p><span className="paragraph-space"></span>{contents.description}</p>
            <p><span className="paragraph-space"></span>{contents.description2}</p>
          </div>

          <div className="portfolioModal-text">
            <h2 className="portfolioModal-text-subtitle">Stacks</h2>
            {stacks.map((stack, index) => { 
              return (
                  <ul className="portfolioModal-ul" key={index}>
                    <li>{stack}</li>
                  </ul>
              )
            })}
          </div>

          <div className="portfolioModal-text">
            <h2 className="portfolioModal-text-subtitle">Reference</h2>
            {reference.map((ref, index) => {
              return (
                <ul key={index} className="portfolioModal-ul">
                  <li>{ref.title}
                  <a className="portfolioModal-urlLink" href={ref.url} 
                  target="_blank" rel="noopener noreferrer">{ref.url}</a>
                  </li>
                </ul>
              )
            })}

              <h4 id="portfolioModal-github-subtext">Github Repository</h4>
              <a className="portfolioModal-urlLink" id="githubRepo" 
              href={githubRepo.client} 
              target="_blank" rel="noopener noreferrer">Client</a>
              <a className="portfolioModal-urlLink" id="githubRepo"
              href={githubRepo.server} 
              target="_blank" rel="noopener noreferrer">Server</a>
              
              <img className="portfolioModal-close-button" 
              src={closebtn} width='30px' height='30px' alt='' 
              onClick={toggleModal}/>
          </div>          
      </div>
      </Modal>
    </div>
  );
}

export default PortfolioModal

