import React from 'react';
import { AboutText, SkillText } from '../portfolio_data/TextData'
import './About.css'

const About = () => {
    return (
        <div id="about" className="section">
            <h1 className="section-title">ABOUT</h1>

            <div className="content-container"> 
            {AboutText.map((element, index) => {
                return (  
                    <div key={index}>
                        <h2 className="about-content-title">{element.title}</h2>
                        {element.contents.map((content, index2) => {
                            return (
                                <div key={index2}>
                                    <p>
                                    <span className="paragraph-space"></span>
                                    {content.text}
                                    </p>
                                </div>
                            )
                        })}
                    </div>                    
                )
            })} 
            </div>

            <div className="content-container" style={{marginBottom: '0'}}>
            <h2 className="about-content-title" style={{marginBottom: '30px'}}>Skills</h2>  
            {SkillText.map((element, index) => {
                return (
                    <div key={index} style={index === 0 ? {marginBottom: '50px'} : {}}>
                    <h3>{element.title}</h3>
                        <p>{element.description}</p>

                        <div style={{marginTop: '30px'}}>
                        {element.stacks.map((stack, index2) => {
                            return (
                                <ul key={index2} className="about-ul">
                                <li>
                                {stack}
                                </li>
                                </ul>
                            )
                        })} 
                        </div>
                    </div>
                )
            })}   
            </div>      
        </div>
    )
}

export default About
